import { UmbElementMixin } from '@umbraco-cms/backoffice/element-api';
import {
	LitElement,
	css,
	customElement,
	html,
	nothing,
	state,
} from '@umbraco-cms/backoffice/external/lit';
import { SyncPublishServerView, USyncPublisherView } from '../../../../api';
import {
	SyncPublisherServerSharedContext,
	USYNC_PUBLISHER_SERVER_SHARED_CONTEXT,
} from '../../server-workspace-shared.context';
import { SyncPublisherYesNoValueChange } from '../../../../components';
import { ManifestPublisherConfig } from '../../../publishers';

@customElement('usync-publisher-server-advanced-view')
export class uSyncPublisherServerAdvancedElement extends UmbElementMixin(LitElement) {
	#sharedContext?: SyncPublisherServerSharedContext;

	@state()
	data?: SyncPublishServerView;

	@state()
	publishers: USyncPublisherView[] = [];

	constructor() {
		super();

		this.consumeContext(USYNC_PUBLISHER_SERVER_SHARED_CONTEXT, async (_context) => {
			if (!_context) return;

			this.#sharedContext = _context;

			this.observe(_context.data, (_data) => {
				this.data = _data;
			});

			this.publishers = await _context.getPublishers();
		});
	}

	#onChange(e: Event) {
		const element = e.target as HTMLInputElement;
		if (!element) return;
		this.#sharedContext?.set({ [element.id]: element.value });
	}

	#onSyncOptionChange(e: CustomEvent<SyncPublisherYesNoValueChange>) {
		if (!e.detail) return;
		this.#sharedContext?.setSyncOption(e.detail.id, {
			value: e.detail.checked,
			editable: e.detail.editable,
		});
	}

	#onSyncOptionBoolChange(e: Event) {
		const element = e.target as HTMLInputElement;
		if (element) {
			this.#sharedContext?.setSyncBoolOption(element.id, element.checked);
		}
	}

	#onPublisherSettingChange(e: Event) {
		const element = e.target as HTMLInputElement;
		if (element) {
			this.#sharedContext?.setPublisherSetting(element.id, element.checked);
		}
	}

	renderDefaultBox() {
		return this.data?.alias != '__default__'
			? nothing
			: html`<div class="default-box">
					<umb-localize key="usyncpublish_defaultSettings"></umb-localize>
				</div>`;
	}

	renderContentOptions() {
		return html`
			<uui-box headline="Content options">
				<umb-property-layout
					label="Include children"
					description="Include the children of the selected item">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeChildren"
							label="includeChildren"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeChildren}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Include media"
					description="Include media items used in selected items">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeMedia"
							label="includeMedia"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeMedia}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Delete missing items"
					description="Remove any items on the target site that are not children of the selected item">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="deleteMissing"
							label="deleteMissing"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.deleteMissing}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>
			</uui-box>
		`;
	}

	renderDeveloperSettings() {
		return html`
			<uui-box headline="Developer settings">
				<umb-property-layout
					label="Include content config"
					description="Configuration on content, such as public access settings, culture and hostnames">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeConfig"
							label="includeConfig"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeConfig}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Include dependencies"
					description="Include settings including doctypes and datatypes">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeDependencies"
							label="includeDependencies"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeDependencies}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Include files"
					description="Include required templates, stylesheets and script files">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeFiles"
							label="includeFiles"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeFiles}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>
			</uui-box>
		`;
	}

	renderPublishSteps() {
		return html`
			<uui-box headline="Push/Pull Behaviour">
				<umb-property-layout
					label="Schedule publish"
					description="Allow user to schedule the publish time">
					<div slot="editor">
						<uui-checkbox
							id="canSchedule"
							label="canSchedule"
							.checked=${this.data?.publisherSettings['canSchedule'] as boolean}
							@change=${this.#onPublisherSettingChange}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Variant publish"
					description="Allow user to select the variant (culture) the publish time">
					<div slot="editor">
						<uui-checkbox
							id="selectVariants"
							label="Select variants"
							.checked=${this.data?.publisherSettings['selectVariants'] as boolean}
							@change=${this.#onPublisherSettingChange}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Ignore content/media sort order"
					description="Ignore the sort order (position) of content and media items during the sync">
					<div slot="editor">
						<uui-checkbox
							id="ignoreSortOrder"
							name="ignore sort order"
							label="Ignore sort order"
							@change=${this.#onSyncOptionBoolChange}
							.checked=${this.data?.sendSettings?.ignoreSortOrder ?? false}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Send edited value"
					description="When syncing send the edited value for a content item, as opposed to the 'published' one.">
					<div slot="editor">
						<uui-checkbox
							id="sendEditedValue"
							name="Send Edited value"
							label="Send Edited value"
							@change=${this.#onSyncOptionBoolChange}
							.checked=${this.data?.sendSettings?.sendEditedValue ?? false}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Fallback to edited values"
					description="If the content being pushed is not published, fallback to using the last 'saved' value for a property">
					<div slot="editor">
						<uui-checkbox
							id="publishFallback"
							name="Publish Fallback"
							label="Publish Fallback"
							@change=${this.#onSyncOptionBoolChange}
							.checked=${this.data?.sendSettings?.publishFallback ?? false}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>
			</uui-box>
		`;
	}

	renderAdditionalOptions() {
		return html`
			<uui-box headline="Additional options">
				<small
					>The following settings can result in a large amount of extra content or media
					being sent during a publish. Most of the time you won't want a user to select
					these settings.</small
				>

				<umb-property-layout
					label="Include ancestors"
					description="Include the parents of selected items">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeAncestors"
							label="includeAncestors"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeAncestors}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Include 'linked pages'"
					description="Include content pages linked to from selected items">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="includeLinked"
							label="includeLinked"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.includeLinked}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>
			</uui-box>
		`;
	}

	renderRestorePoints() {
		return html`
			<uui-box headline="Restore points">
				<umb-property-layout
					label="Create restore points"
					description="Create a restore point before making any changes">
					<div slot="editor">
						<usync-publisher-yes-no-server-option
							id="createRestorePoint"
							label="createRestorePoint"
							@value-change=${this.#onSyncOptionChange}
							.value=${this.data?.sendSettings
								?.createRestorePoint}></usync-publisher-yes-no-server-option>
					</div>
				</umb-property-layout>

				<umb-property-layout
					label="Restore detection"
					description="Prompt to create a restore point when potential 'damaging' sync operations are performed">
					<div slot="editor">
						<uui-checkbox
							id="detectRestorePoint"
							name="ignore sort order"
							label="Detect restore point"
							@change=${this.#onSyncOptionBoolChange}
							.checked=${this.data?.sendSettings?.detectRestorePoint ?? false}>
							<div slot="label"></div
						></uui-checkbox>
					</div>
				</umb-property-layout>
			</uui-box>
		`;
	}

	renderPublisherOptions() {
		return html` <uui-box>
			<umb-property-layout label="Publisher" description="publisher to use">
				<div slot="editor">${this.renderPublishers()}</div>
			</umb-property-layout>

			<umb-property-layout label="Base URL" description="Override URL in request">
				<div slot="editor">
					<uui-input
						type="text"
						label="Base URL"
						id="baseUrl"
						.value=${this.data?.baseUrl ?? ''}
						@input=${this.#onChange}></uui-input>
				</div>
			</umb-property-layout>
		</uui-box>`;
	}

	render() {
		return html`
			${this.renderDefaultBox()}
			<umb-body-layout>
				<div class="wrapper">
					<div class="two-by-two">
						${this.renderContentOptions()} ${this.renderDeveloperSettings()}
						${this.renderPublishSteps()} ${this.renderAdditionalOptions()}
					</div>

					${this.renderPublisherOptions()}${this.renderPublisherConfig()}
					${this.renderRestorePoints()}
				</div>
			</umb-body-layout>
		`;
	}

	renderPublisherConfig() {
		if (!this.data?.publisher) return nothing;

		const publisher = this.publishers.find((p) => p.alias === this.data?.publisher);
		if (!publisher || !publisher.configElement) return nothing;

		return html`<uui-box .headline="${publisher.name} settings">
			<umb-extension-slot
				type="usync-publisher-config"
				.filter=${(ext: ManifestPublisherConfig) =>
					ext.elementName === publisher.configElement}
				.props=${{ options: this.data }}></umb-extension-slot>
		</uui-box> `;
	}

	renderPublishers() {
		const publishers = this.publishers.map((publisher) => {
			return {
				name: publisher.name,
				value: publisher.alias,
				selected: publisher.alias === this.data?.publisher,
			};
		});

		return html` <uui-select
			id="publisher"
			label="publisher"
			.value="${this.data?.publisher}"
			.options="${publishers}"
			@change=${this.#onChange}></uui-select>`;
	}

	static styles = css`
		.wrapper {
			display: flex;
			gap: var(--uui-size-space-4);
			flex-direction: column;
		}

		.two-by-two {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: var(--uui-size-space-4);
		}

		uui-input {
			width: 100%;
		}

		uui-select {
			width: 100%;
		}

		.default-box {
			text-align: center;
			padding: 10px;
			background-color: var(--uui-color-border);
		}
	`;
}

export default uSyncPublisherServerAdvancedElement;
